import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import Notes from "../notes";

function Create(noteitem) {
  return (
    <Note
      key={noteitem.key}
      title={noteitem.title}
      content={noteitem.content}
    />
  );
}

function App() {
  return (
    <div>
      <Header />
      {Notes.map(Create)}
      <Footer />
    </div>
  );
}

export default App;
